import "./styles.css";
import { useEffect, useState } from "react";
import moment from "moment";
import { animated, useSpring } from "react-spring";

export default function App() {
  const [like, setLike] = useState(false);
  const [likeCount, setLikeCount] = useState(0);
  const [nasa, setNasa] = useState({
    title: "",
    url: "",
    explanation: "",
    data: "",
    copyright: ""
  });

  useEffect(() => {
    getNasa();
  }, []);

  const getNasa = async () => {
    try {
      const response = await fetch(
        "https://api.nasa.gov/planetary/apod?api_key=Vjn6p0QdbieaWfcv4udkKUxjQgghOvYhfbOXNMyx"
      );
      const data = await response.json();
      setNasa({ ...data });
    } catch (err) {}
  };

  useEffect(() => {
    heart();
  }, [like]);

  const heart = () => {
    let x = 0;
    if (like === true) {
      setLikeCount((x = +1));
    } else {
      setLikeCount(0);
    }
  };

  const BigHeart = () => {
    const styles = useSpring({
      pause: !like,
      margin: 1,
      from: {
        border: "red",
        color: "pink",
        scale: 1.4,
        transform: "rotateY(0deg)"
      },
      to: [
        { color: "red", scale: 5, transform: "rotateZ(360deg)" },
        { color: "red", scale: 1.4, transform: "rotateY(0deg)" }
      ]
    });
    return (
      <animated.i style={styles} className={`p-1 fas fa-heart `}></animated.i>
    );
  };

  return (
    <div className="App">
      <h1 id="challange">Shopify Front-End Internship</h1>
      <div
        className="card mx-auto shadow p-3 mb-5 bg-white rounded "
        style={{ width: "50%", height: "50%" }}
      >
        <img className="card-img-top " src={nasa.url} alt="" />
        <h1 id="title" className="card-title p-2 ">
          {nasa.title}
        </h1>
        <div className="card-body  ">
          <p id="explanation " className="card-text">
            {nasa.explanation}
          </p>
          <p id="copyright" className="font-weight-light">
            Credit to: {nasa.copyright}
          </p>
          <p id="date">{moment(nasa.data).format("MMMM Do YYYY")}</p>
          <div className="d-flex justify-content-around">
            <button
              className="m-1 btn align-self-center btn-outline-danger"
              onClick={() => {
                setLike(!like);
              }}
            >
              <BigHeart />
              {likeCount > 0 ? <>{likeCount}</> : <></>}
            </button>
            <button
              className="m-1 align-self-center btn btn-primary"
              onClick={heart}
            >
              <i className={`fas fa-comment `}></i>
            </button>
            <button
              className="m-1 align-self-center btn btn-primary"
              onClick={heart}
            >
              <i className={`fas fa-share`}></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
